import React, { Component } from 'react'
import gosi from "../../assets/gosi.png"
import gosi2 from "../../assets/gosi2.png"
import './Challenge.scss'
export default class Challenge extends Component {
    render() {
        return (
            <div className="Challenge">
                <div className="tools-challenge">
                    <div className="bt"></div>
                    <h2 id="Overview" style={{marginTop: "0px"}}>Overview</h2>
                    <p>Along with the increasing use of digital cameras, large volumes of videos have been produced in the city. These videos play critical roles in city management, public safety, traffic control, and environment protection etc. One critical task in utilizing these videos is detecting anomalous events such as traffic accidents, crimes or illegal activities. It is unrealistic for humans to screen such big data to extract anomalous events since anomalous events rarely occur as compared to normal activities. Hence research on anomalous event detection is of paramount importance, which is thus a field that has attracted attentions in both academia and industry. However, detecting a variety of anomalous events in these real-world videos is a challenging problem due to the complexity of “anomaly” as well as the diverse backgrounds, imaging quality and motions in the city scenes.</p><p>To encourage the research community to address the challenges of anomalous event detection in real-world videos, we propose to run a video anomaly detection challenge at ACM Multimedia 2020. We collected a new challenging dataset, namely CitySCENE, comprising a set of real-world videos. CitySCENE is a new video dataset which consists of more than 1000 untrimmed real-world videos, with a set of realistic anomalies including quarrelling, fighting, robbery, stealing, explosion, road_accident, vandalism, falling, smoggy, littering, smoking, talking_phone, texting_phone, road_sweeper_cleaning, chasing, walking_dog, loading, unloading, footpath_trading etc. These anomalies are selected because they have a significant impact on city management and public safety. We hope that the dataset and challenge will serve as a common tool for researchers to benchmark their algorithms with each other and thus contribute towards reproducible research.</p><h2 id="Task">Task Description </h2><p>CitySCENE dataset contains a set of untrimmed real-world videos with a set of realistic anomalies. This dataset can be used for two tasks: (1) general anomaly detection, which treats all anomalies in one group and all normal events in the other group; and (2) specific anomaly detection, which recognizes each of the anomalous activities.</p>
                    <h3 className="task1">Task 1: General Anomaly Detection</h3>
                    <p>General anomaly detection can be seen as coarse level video understanding. It aims at identifying the time window of the occurring anomalous events within a video, which filters out anomalies from normal scenes. Once an anomalous event is detected, it can further be categorized into one of the predefined categories using classification techniques.</p>
                    <h3 className="task2">Task 2: Specific Anomaly Detection</h3><p>A step towards addressing anomalous event detection is to detect a specific anomalous event within a video, for example fighting and smoking detector.</p>
                    <h2>Evaluation Metric</h2>
                    <p>We use frame-based receiver operating characteristic (ROC) curve and corresponding area under the curve (AUC) to evaluate the performance of the method. An ROC space is defined by FPR and TPR as x and y axes, respectively, which depicts relative trade-offs between true positive (benefits) and false positive (costs).</p>
                    <p style={{paddingLeft: "64px",textAlign: "center"}}>
                        <img src={gosi2} alt="" />
                    </p>                   
                    <p style={{paddingLeft: "64px",textAlign: "center"}}>
                        <img src={gosi} alt="" />
                    </p>
                </div>
            </div>
        )
    }
}
